import IMedia2 from '@/components/cell/i-media2'
import {clear_tags} from '@/plugins/utils'

export default {
  name: "EraxNFT",
  props: {
    item: {
      type: Object,
      required: true
    },
    idx: {
      type: Number
    },
    USDT: {
      type: Object,
      required: true
    },
    dictData: {
      type: Object,
      required: true
    }
  },
  components: {
    IMedia2
  },
  data() {
    this.clearTags = clear_tags
    return {
      v: this.item,
      intervalTimeID: null,
      leftTime: 0,
      isShowLine4: false
    }
  },
  computed: {
    calcBidTime() {
      if (this.leftTime < 86400 && this.leftTime > 0) {
        return this.$util.formatTime000000(this.leftTime);
      }
      return ''
    },
    dPrice() {
      if (this.v.stCurrent.dPrice) {
        return parseFloat(this.v.stCurrent.dPrice).toFixed(2)
      }

    }
  },
  watch: {
    item(val) {
      this.v = val
    },
    USDT(v) {
      return v
    },
    dictData(v) {
      return v
    },
  },
  async mounted() {


    this.intervalTimeID = setInterval(() => {
      this.leftTime = (this.v.stCurrent.iExpirationDate - new Date()) / 1000;
      if (this.leftTime <= 0) {
        clearInterval(this.intervalTimeID)
      }
    }, 1000);
  },
  methods: {
    handleLoadMore() {
      this.isShowLine4 = !this.isShowLine4
      if (this.isShowLine4) {
        this.$set(this.v, 'isLoading', true)
        this.$emit('callBack', this.idx)
      }
    },
    getDoll(v) {
      let key = this.dictData[v.sCurrency]
      // console.log(this.USDT);
      switch (key) {
        case 'ETH':
        case 'WETH':
          return (this.USDT['ETHUSDT'] * parseFloat(v.dPrice)).toFixed(2);
        case 'BRD':
          return (this.USDT['BRDBTC'] * this.USDT['BTCUSDT'] * parseFloat(v.dPrice)).toFixed(2);
        case 'NERA':
        case 'ERAT':
          return (this.USDT['NERAUSDT'] * parseFloat(v.dPrice)).toFixed(2);

        default:
          return '';
      }
    },
  },
  beforeDestroy() {
    clearInterval(this.intervalTimeID)
  }
}
